header {
  color: white;
  text-align: left;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  background: black;
  background: -webkit-linear-gradient(315deg, white 0%, black 50%, white 100%);
  background: -o-linear-gradient(315deg, white 0%, black 50%, white 100%);
  background: linear-gradient(135deg, white 0%, black 50%, white 100%);
  height: 2.6rem;
}
header a {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  padding: 0 0.5rem;
  cursor: pointer;
  text-decoration: none;
  color: white;
  -webkit-transition: all 100ms;
  -o-transition: all 100ms;
  transition: all 100ms;
  text-shadow: 1px 1px 3px hsla(0, 0%, 35%, 1);
}
header a:hover {
  text-shadow: 2px 2px 4px hsla(0, 0%, 15%, 1);
}
header a.active {
  text-shadow: 3px 3px 15px hsla(0, 0%, 0%, 1);
}
header .title {
  margin: 0;
  padding-left: 0.5rem;
  height: 100%;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
}

/* 384px */
@media only screen and (min-width: 24em) {
  header a {
    padding: 0 0.7rem;
  }
}



