.preview {
  width: 100%;
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  /*background: #ddd;*/
  /*background: linear-gradient(180deg, #fff 0%, #eee 80%, #aaa 100%);*/
  background: white;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
}

.preview:nth-child(2n) {
  background: rgb(181, 181, 220);
}

@media screen and (min-width: 52em) {
  .preview {
    -ms-flex-direction: row;
        flex-direction: row;
    min-height: 30rem;
  }
}

.about {
  width: 90%;
  height: 50%;
  margin: 2rem 2rem 0 0;
  padding: 1rem 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

@media screen and (min-width: 52em) {
  .about {
    height: 100%;
    margin-top: 0;
    padding: 5rem 0;
  }
}

@media screen and (min-width: 72em) {
  .about {
    width: 100%;
  }
}

.preview .title,
.preview .description,
.preview .moreDescription {
  text-align: left;
  padding: 0 1rem 0.5rem;
}

.preview .title {
  font-size: 2rem;
}

.preview .description {
  z-index: 1;
  bottom: 0;
  width: calc(100% - 2rem);
}

.preview .moreDescription {
  margin: 0;
  padding: 0 1rem 5rem;
}

.preview .moreDescription.desktop {
  display: none;
  margin: 0;
  padding-bottom: 0;
}

.preview .moreDescription > * {
  margin: 1rem auto;
}

@media screen and (min-width: 52em) {
  .preview .moreDescription {
    margin: 2rem 2rem 4rem;
  }
  .preview .moreDescription.mobile {
    display: none;
  }
  .preview .moreDescription.desktop {
    display: block;
  }
}

.preview .centeringContainer {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}



