.videoContainer {
  min-width: 200px;
  max-width: calc(100vw - 2rem);
  overflow: hidden;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  -webkit-transition: all 100ms ease-in;
  -o-transition: all 100ms ease-in;
  transition: all 100ms ease-in;
  cursor: pointer;
  -webkit-box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.2);
          box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.2);
}

.videoContainer:hover {
  -webkit-box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.8);
          box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.8);
}

.videoContainer.active:hover {
  -webkit-box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.2);
          box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.2);
}

.thumbnailContainer {
  display: block;
  position: relative;
  overflow: hidden;
  height: 175px;
  width: 290px;
  cursor: pointer;
  outline: none;
}

@media screen and (min-width: 25em) {
  .thumbnailContainer {
    height: 190px;
    width: 340px;
  }
}

@media screen and (min-width: 32em) {
  .thumbnailContainer {
    height: 270px;
    width: 450px;
  }
}

@media screen and (min-width: 62em) {
  .thumbnailContainer {
    height: 300px;
    width: 500px;
  }
}

.videoContainer.active .thumbnailContainer {
  cursor: default;
}

.thumbnail {
  -webkit-transition: all 400ms ease-out;
  -o-transition: all 400ms ease-out;
  transition: all 400ms ease-out;
  margin-top: -45px;
  width: 100%;
  height: calc(100% + 82px);
}

.videoContainer.active .thumbnailContainer .thumbnail {
  opacity: 0.2;
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.youtubeLogo {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  height: 30px;
  -webkit-transition: opacity 400ms ease-out, -webkit-filter 200ms;
  transition: opacity 400ms ease-out, -webkit-filter 200ms;
  -o-transition: opacity 400ms ease-out, filter 200ms;
  transition: opacity 400ms ease-out, filter 200ms;
  transition: opacity 400ms ease-out, filter 200ms, -webkit-filter 200ms;
  opacity: 1;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.thumbnailContainer:hover .youtubeLogo {
  -webkit-filter: none;
          filter: none;
}

.videoContainer > iframe {
  position: absolute;
  top: 1rem;
  left: 1rem;
  width: calc(100% - 2rem);
  height: calc(100% - 2rem);
}

.videoContainer.active .thumbnailContainer .youtubeLogo {
  opacity: 0;
}

.videoContainer.active iframe {
  -webkit-transition: all 100ms ease-in;
  -o-transition: all 100ms ease-in;
  transition: all 100ms ease-in;
  -webkit-box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.5);
          box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.5);
}

.videoContainer.active iframe:hover {
  -webkit-box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.8);
          box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.8);
}



