.header-menu {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: right;
      justify-content: right;
  padding: 0 0.5rem;
}

/* 384px */
@media only screen and (min-width: 24em) {
  .header-menu {
    padding: 0 1rem;
  }
}

/* 576px */
@media only screen and (min-width: 36em) {
  .header-menu {
    right: 3rem;
  }
}



