.linksbar {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: 0 1rem;
}

a.link {
  display: inline-block;
  height: 2rem;
}

a.link img {
  opacity: 0.5;
  -webkit-transition: opacity 200ms;
  -o-transition: opacity 200ms;
  transition: opacity 200ms;
  height: 100%;
}

a.link:hover img {
  opacity: 1;
}



