.imageContainer {
  height: 200px;
  overflow: hidden;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  outline: none;
  -webkit-box-shadow: 3px 3px 8px rgba(100, 100, 100, 0.2);
          box-shadow: 3px 3px 8px rgba(100, 100, 100, 0.2);
  -webkit-transition: all 100ms ease-in;
  -o-transition: all 100ms ease-in;
  transition: all 100ms ease-in;
}

.imageContainer:hover {
  -webkit-box-shadow: 3px 3px 8px rgba(100, 100, 100, 0.5);
          box-shadow: 3px 3px 8px rgba(100, 100, 100, 0.5);
}

@media screen and (min-width: 25em) {
  .imageContainer {
    height: 220px;
  }
}

@media screen and (min-width: 32em) {
  .imageContainer {
    height: 270px;
  }
}

@media screen and (min-width: 62em) {
  .imageContainer {
    height: 300px;
  }
}

.imageContainer a {
  display: block;
  position: relative;
  overflow: hidden;
  height: 100%;
  cursor: pointer;
  outline: none;
}

.imageContainer a img {
  height: 100%;
}

.modalWrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(100, 100, 100, 0.4);
  z-index: 1000;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.modalWrapper.hidden {
  display: none;
}

.modalDialog {
  -webkit-box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.5);
          box-shadow: 2px 2px 10px rgba(50, 50, 50, 0.5);
  margin: 1rem;
  max-height: 90%;
}



