.App {
  text-align: center;
  font-family: sans-serif;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* For animating transitions between routed components */

.switch-wrapper {
  position: relative;
  margin-top: 2.5rem;
  height: calc(100% - 2.5rem); /* Subtract header height */
}

.switch-wrapper > div {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.switch-wrapper > div:first-child {
  position: relative;
  height: 100%;
}



