.projects .container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.group {
  position: relative;
  width: 100%;
  height: 300px;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0;
  cursor: pointer;
  font-family: Calibri Light;
  font-size: 1.5em;
  overflow: hidden;
}

.group .title {
  position: absolute;
  top: 0;
  font-weight: bold;
  font-size: 2rem;
  /*text-shadow: 1px 1px 3px white;*/
  bottom: 0;
  left: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
  background-color: rgba(200, 200, 200, 0.5);
  color: black;
  text-shadow: 0px 0px 20px white;
}

.group:nth-child(2n) .title {
  background-color: rgba(181, 181, 220, 0.8);
  color: white;
  text-shadow: 0px 0px 20px black;
}

.group:hover .title {
  background-color: rgba(102, 102, 134, 0.9);
  color: white;
  text-shadow: 0px 0px 20px black;
}

@media only screen and (min-width: 36em) {
  .projects .container {
    padding: 1rem 0;
  }

  .group {
    width: calc(50% - 3rem);
    border: 2px solid rgb(102, 102, 134);
    margin: 1rem;
  }

  .group:nth-child(2n) .title {
    background-color: rgba(181, 181, 220, 0.3);
    color: black;
    text-shadow: 0px 0px 20px white;
  }

  .group:nth-child(2n):hover .title {
    color: white;
    text-shadow: 0px 0px 20px black;
  }

  .group:hover .title {
    background-color: rgba(181, 181, 220, 0.7);
  }
}

@media only screen and (min-width: 46em) {
  .projects .container {
    padding: 5rem 1rem;
  }

  .group {
    width: 300px;
  }
}

@media only screen and (min-width: 56em) {
  .projects .container {
    padding: 5rem 5rem;
  }
}

.group .img-wrapper {
  height: 50%;
  width: 50%;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}

.group .img-wrapper img {
  max-height: 100%;
}



