footer {
  background: rgb(102, 102, 134);
  padding: 1rem 1rem 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

/* 576px */
@media only screen and (min-width: 36em) {
  footer {
    -ms-flex-direction: row;
        flex-direction: row;
  }
}

footer a {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  padding: 0 0.5rem;
  cursor: pointer;
  text-decoration: none;
  color: white;
  -webkit-transition: all 100ms;
  -o-transition: all 100ms;
  transition: all 100ms;
  text-shadow: 1px 1px 3px hsla(0, 0%, 35%, 1);
}
footer a:hover {
  text-shadow: 2px 2px 4px hsla(0, 0%, 15%, 1);
}

.headline {
  margin: 1rem;
}

.subline {
  margin: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  color: rgb(200, 200, 200);
}

.subline a {
  color: rgb(200, 200, 200);
}

/* 384px */
@media only screen and (min-width: 24em) {
  footer a {
    padding: 0 0.7rem;
  }
}



